	module.exports = {"data":{"site":{"siteMetadata":{"title":"GREG JOHNS","author":"Greg Johns"}},"markdownRemark":{"id":"/home/greg/Projects/Greg-Johns.github.io/src/pages/2018-01-24-teaparty/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/teaparty-d9f33fd187b773b32d909ab223c1190e-c7682.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 128.73900293255133%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"TeaParty\"\n        title=\"\"\n        src=\"/static/teaparty-d9f33fd187b773b32d909ab223c1190e-f8fb9.jpg\"\n        srcset=\"/static/teaparty-d9f33fd187b773b32d909ab223c1190e-e8976.jpg 148w,\n/static/teaparty-d9f33fd187b773b32d909ab223c1190e-63df2.jpg 295w,\n/static/teaparty-d9f33fd187b773b32d909ab223c1190e-f8fb9.jpg 590w,\n/static/teaparty-d9f33fd187b773b32d909ab223c1190e-c7682.jpg 682w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Poster design for Math Department</h4>\n<p>My cousin, who teaches math at <em>The University of Scranton,</em> called me a while back and wanted to know if I would come up with a poster to put up around the school campus that let students know about the math departments upcomming social event. Of course it was hard to resist the idea to riff off of Alice’s teaparty but that seemed a bit trite and also open to wide interpretations. So I went with a more <em>geometric</em> approach. The result is the image above.</p>\n<p>Small as it was, I enjoy a good print design job every now and then. Working as a desktop print designer was how I got my professional start as a designer and I love the maths. Also, it was just fun to think about advertising a math tea party. I was happy with the result for the couple hours I spent on it so decided to post it here.</p>\n<blockquote>\n<p>FUN FACT: The wireframe for the teapot is in fact the <a href=\"https://en.wikipedia.org/wiki/Utah_teapot\">Utah teapot</a></p>\n</blockquote>","frontmatter":{"title":"TeaParty","date":"January 24, 2018"}}},"pathContext":{"path":"/teaparty/"}}

